import { Formio } from '@formio/react';

const getQuery = (query) => {
  window.location.search.substr(1).split('&').forEach(function(item) {
    query[item.split('=')[0]] = item.split('=')[1] && decodeURIComponent(item.split('=')[1]);
  });

  return query;
}

const stages = {
  dev: 'https://formbuilding-izshuiourtmigzc.form.io',
  test: 'https://testoauth-izshuiourtmigzc.form.io',
  prod: 'https://izshuiourtmigzc.form.io',
};

const api = {
  portal: 'https://api.form.io',
}

const query = getQuery({});
const PROJECT_URL = query.projectUrl || stages.test;
const API_URL = query.apiUrl || api.portal;

export const AppConfig = {
  projectUrl: PROJECT_URL,
  apiUrl: API_URL,
  version: '0.0.0'
};

export const AuthConfig = {
  anonState: '/auth',
  authState: '/',
  login: {
    form: 'user/login'
  },
  adminLogin: {
    form: 'admin/login'
  },
  register: {
    form: 'user/register'
  }
};

export const FormsConfig = {
  formsFilter: 'formsfilter',
  formsTable: 'formstable',
  adminFormsTable: 'adminformstable',
  submissionsTable: 'submissionTable',
  namedSubmissionsTable: 'namedSubmissionTable',
  user: 'user',
  userProfile: 'userprofile',
  folderForm: 'folderform',
  userGroup: 'usergroup',
  usersGroup: 'usersGroup',
  folder: 'folder',
  homePage: 'homepage',
  myForms: 'myforms',
  myTags: 'mytags',
  myFormsTable: 'myformstable',
  myFormsFilter: 'myformsfilter',
  submissionName: 'submissionName',
  route: 'route',
  routeTrack: 'submissionRouteTrack',
  routedSubmissionsTable: 'routedsubmissionstable',
  submissionAccessData: 'submissionaccessdata',
  processSettings: 'processsettings',
  processSupplement: 'processsupplement',
  allSpecial: 'allspecial',
  csvFilters: 'csvfilters',
  getSrc: (formName) => PROJECT_URL + '/' + FormsConfig[`${formName}`],
};

Formio.setProjectUrl(AppConfig.projectUrl);
Formio.setBaseUrl(AppConfig.apiUrl);
