import React, { useEffect, useCallback, useState} from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { Errors, Form } from '@formio/react';
import _ from 'lodash';

import { Loading, Title } from '../../../../common';
import { useForm } from '../../form';
import { useSubmissions, indexSubmissions, saveSubmissions } from '../submissionsContext';
import { FormsConfig } from '../../../../config';
import { ModalConfirmation } from '../../partials';
import { addSelectedRowsClass, hasAdminRights, setSubmissionStatus, submissionStatuses } from '../../../../utils';
import { useAlerts } from '../../../alerts';
import { useAuth } from '../../../auth';


const NamedSubmissionsList = (props) => {
  const {
    FormError,
    formName,
    getViewPath,
    createSubmissionPath,
    addBtnTitle,
    id,
    submissionsRequestParams = {},
    title,
    hideAddBtn
  } = props;

  const history = useHistory();
  const formId  = useParams().formId || id;

  const defaultRequestParams = {
    limit: 100000,
    query: {},
    select: '',
    sort: '-modified',
    ...submissionsRequestParams
  };
  
  const [requestParams, setRequestParams] = useState(defaultRequestParams);
  const [paramsSet, setParamsSet] = useState(false);

  const [statusAction, setStatusAction] = useState({
    active:false,
    selectedIds: [],
    delete: false,
    archive: false
  });
  const { state: submissionsState, dispatch: dispatchSubmissionsAction } = useSubmissions();
  const { state: formState} = useForm();
  const { addAlert } = useAlerts();

  const {state: authState} = useAuth();
  const {is: role } = authState;
  const isAdmin = hasAdminRights(role);

  useEffect(()=> {
    setRequestParams((requestParams) => {
      const updatedRequestParams = {...requestParams, ...submissionsRequestParams};
      setParamsSet(true);

      return !_.isEqual(requestParams, updatedRequestParams) 
        ? updatedRequestParams
        : requestParams;
    });
  }, [submissionsRequestParams]);

  const getSubmissions = useCallback(
    (page, query) => {
      indexSubmissions(dispatchSubmissionsAction, page, requestParams, query, formId, formName);
    },
    [dispatchSubmissionsAction, formId, formName, requestParams]
  );

  useEffect(() => {
   getSubmissions(1);
  }, [getSubmissions, formId,requestParams]);

  const isLoading = formState.isActive || submissionsState.isActive || !paramsSet;

  if (isLoading) {
    return <Loading />;
  }

  const gridSubmissions = submissionsState.submissions.map(subm => ({...subm, ...subm.metadata}));

  const onNo = () => {
    setStatusAction({
      active: false,
      selectedIds: statusAction.selectedIds,
      delete: false,
      archive: false
    })
  };

  const onYes = () => {
    const {selectedIds, archive, delete: deleteAction} = statusAction
    const newStatus = archive 
      ? submissionStatuses.archived
      : deleteAction 
        ? submissionStatuses.deleted 
        : '';

    const updatedSubmissions = _.chain(submissionsState.submissions)
      .filter(subm => _.some(selectedIds, id => subm._id === id))
      .each(subm => setSubmissionStatus(subm, newStatus))
      .value();

    saveSubmissions(dispatchSubmissionsAction, updatedSubmissions, formId, null, (err) => {
      if(!err) {
        getSubmissions(1);
        setStatusAction({
          active: false,
          selectedIds: [],
          delete: false,
          archive: false
        });
        addAlert({ type: 'success', content: `Form Submissions are successfully moved to  the ${(deleteAction && 'Deleted') || (archive && 'Archived')}`}); 
      }
    });
  }

  const Grid = () => (
    <Form
      submission={{data: {submissions: gridSubmissions, selectedSubmissions: statusAction.selectedIds}}}
      src={FormsConfig.getSrc('namedSubmissionsTable')}
      options={{noSubmit: true, hide: {deletePermanently: true, select: isAdmin, moveToCurrent: true, track:true}, ...formState.options}}
      formReady={(form) => {
        form.on('open', ({_id: submissionId})=> {
          if (submissionId) {
            history.push(getViewPath ? getViewPath(formId, submissionId) : `/form/${formId}/submission/${submissionId}`);
          }
        });

        form.on('page', ()=> {
          addSelectedRowsClass(form, form.data.selectedSubmissions);
        });

        form.on('deselectAll', () => {
          addSelectedRowsClass(form, []);
        });

        form.on('moveToArchived', () => {
          setStatusAction({
            active: true,
            selectedIds:  form.data.selectedSubmissions || [],
            delete: false,
            archive: true
          });
        });

        form.on('moveToDeleted', () => {
          setStatusAction({
            active: true,
            selectedIds:  form.data.selectedSubmissions || [],
            delete: true,
            archive: false
          })
        });
 
        form.on('selectRow', (data)=> {
          const selectedSubmissions = form.data.selectedSubmissions;

          form.data.selectedSubmissions = _.includes(selectedSubmissions, data._id )
            ? [..._.filter(selectedSubmissions, id => id !== data._id)]
            : [...selectedSubmissions, data._id];

          addSelectedRowsClass(form, form.data.selectedSubmissions);
          form.triggerChange();
        });
      }}
    />
  );

  const titleText = title || `Current Submissions - ${formState.form.title} Form `;

  const AddBtn = () => {
    return hideAddBtn
      ? null
      : (<div className="d-flex justify-content-end mb-2">
          <Link className='btn btn-primary' to={createSubmissionPath || `/form/${formId || formState.id}`}>
            <i className='glyphicon glyphicon-plus fa fa-plus' aria-hidden='true'></i>
            &nbsp; {addBtnTitle || 'New ' + formState.form?.title}
          </Link>
        </div>)
  };

  const MainContent = () =>  (
    <div className='form-index position-relative'>
      {statusAction.active && <ModalConfirmation
        onNo={onNo}
        onYes={onYes}
        message={(statusAction.archive && <span>Are you sure you wish to move <strong>{statusAction.selectedIds.length}selected form submission(s)</strong>  to the <strong>Archived</strong>?</span>) ||
        (statusAction.delete && <span>Are you sure you wish to move <strong>{statusAction.selectedIds.length} selected form submission(s)</strong> to the <strong>Deleted</strong>?</span>)}
        onModalCloseClick={onNo}
      />}
      <Errors errors={[formState.error, submissionsState.error]} />
      <Title text={titleText} />
      <AddBtn/>
      {!_.isEmpty(gridSubmissions) ? <Grid/> : 'No items to display.'}
    </div>
  );

  if (formState.error && FormError) {
    return(
      <FormError error={formState.error}>
        <MainContent />
      </FormError>
    );
  }

  return <MainContent />;
}

export default NamedSubmissionsList;
