import _ from 'lodash';
import React, { useEffect } from 'react';
import { Route, Switch } from 'react-router-dom';
import { Loading } from '../../../../common';

import { FormsConfig } from '../../../../config';
import { FormView, getForm, useForm } from '../../../forms/form';
import { FormsProvider } from '../../../forms/form/formsContext';
import { SubmissionProvider, SubmissionsList, SubmissionsProvider } from '../../../forms/submission';
import { FolderProvider } from '../folderContext';
import FolderPage from './FolderPage';

const FoldersPage = (props) => {
  const paramName = 'folder';
  const pathStart = '/folders';
  const formName = FormsConfig.folder;

  const { dispatch: dispatchFormEvent, state: formState } = useForm();
  const {form, isActive} = formState;

  useEffect(() => {
    getForm(dispatchFormEvent, null, formName);
  }, [dispatchFormEvent, formName]);

  if (isActive || _.isEmpty(form)) {
    return <Loading/>
  }

  return (
    <FormsProvider>
      <SubmissionsProvider>
        <Switch>
          <Route
            exact
            path={`${pathStart}`}
            render={(props) => {
              return  <SubmissionsList
                {...props}
                formName={formName}
                getViewPath={(formId, submission) => `${pathStart}/${submission._id}`}
                getEditPath={(formId, submission) => `${pathStart}/${submission._id}/edit`}
                getDeletePath={(formId, submission) => `${pathStart}/${submission._id}/delete`}
                createSubmissionPath={`${pathStart}/create`}
                submissionsRequestParams={{sort: 'data.folderPath'}}
                pathStart={pathStart}
                addBtnTitle={'Folder'}
                title={'Folders'}
              />
            }}
          />
          <Route
            exact
            path={`${pathStart}/create`}
            render={(props) => (
              <SubmissionProvider>
                <FormView {...props} name={formName} pathStart={pathStart}/>
              </SubmissionProvider>
            )}
          />
          <Route
            path={`${pathStart}/:${paramName}Id`}
            render={(props) => (
              <SubmissionProvider>
                <FolderProvider>
                  <FolderPage {...props} pathStart={pathStart} paramName={paramName} formName={formName} />
                </FolderProvider>
              </SubmissionProvider>
            )}
          />
        </Switch>
      </SubmissionsProvider>
    </FormsProvider>
  );
};



export default FoldersPage;
