import { Formio } from '@formio/react';
import _ from 'lodash';
import {FormsConfig} from '../../../config';
import {Utils} from 'formiojs';
import {requestForms, receiveForms,failForms} from '../../forms/form/formsContext'
import { getRequestParams } from '../../../utils';

const transformFormsResult = (forms) => {
  forms = Utils.fastCloneDeep(forms)

  if (forms && forms.length) {
    return _.map(forms, form => ({formName: _.get(form, 'data.form.data.form.name',''), formTitle: _.get(form, 'data.form.data.form.title',''), formId:_.get(form, 'data.form.data.form._id',''), 
    tags: _.map(form.data.tags || [], (tag)=> _.get(tag, 'data.tag', null))}))
  }
  return [];
};

export const getMyForms = (dispatch, { limit, query, select, sort }, page = 1, params = {}, done = () => {}) => {
  dispatch(requestForms(page, params));

  const formio = new Formio(`${Formio.getProjectUrl()}/${FormsConfig.myForms}/submission`);
  const selectData = 'data.form.data.form,data.tags';
  select = select ? `${select},${selectData}` : selectData;
  sort ='-modified';

  const requestParams = getRequestParams(limit, query, sort, params, select, page);

  return formio.loadSubmissions({ params: requestParams })
    .then((result) => {
      dispatch(receiveForms(transformFormsResult(result), limit));
      done(null, result);
    })
    .catch((error) => {
      dispatch(failForms(error));
      done(error);
    });
};
