import React, { useEffect, useCallback, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Errors, Form} from '@formio/react';
import _ from 'lodash';

import { Loading, Title } from '../../../../common';
import { useForm } from '../../form';
import { useSubmissions, indexSubmissions, saveSubmissions } from '../../submission/submissionsContext';
import { FormsConfig } from '../../../../config';
import { useAuth } from '../../../auth';
import { ModalConfirmation, RouteTrackingModal } from '../../partials';
import { addSelectedRowsClass, removeRouteTrackArchiveUser, setRouteTrackArchiveUser } from '../../../../utils';
import { useAlerts } from '../../../alerts';


const IncomingSubmissionsList = (props) => {
  const {
    FormError,
    archived,
    id,
    submissionsRequestParams,
    hideTitle
  } = props;
  const selectedRowIdProperty = 'routeTrackId';

  const history = useHistory();
  const formId  = useParams().formId || id;
  const {state: authState} = useAuth();
  const {user} = authState;
  const defaultRequestParams = {
    limit: 100000,
    query: {
      ...(formId ? {'data.routedForm.id': formId} : {}),
      'data.currentRoute.owners._id': user._id,
      [`data.currentRoute.archiveUsers._id${archived ? '':'__ne'}`]: user._id,
    },
    select: '',
    sort: '-data.currentRoute.date',
    ...submissionsRequestParams
  };

  const defaultStatusAction = {
    active:false,
    selectedIds: [],
    archive: false,
    restore: false,
  };
  
  const [requestParams, setRequestParams] = useState(defaultRequestParams);
  const [paramsSet, setParamsSet] = useState(false);
  const [trackingSubmission, setTrackingSubmission] = useState(null);
  const [statusAction, setStatusAction] = useState(defaultStatusAction);

  const { state: submissionsState, dispatch: dispatchSubmissionsAction } = useSubmissions();
  const { state: formState} = useForm();
  const { addAlert } = useAlerts();

  useEffect(()=> {
    setRequestParams((requestParams) => {
      const updatedRequestParams = {...requestParams, ...submissionsRequestParams};
      setParamsSet(true);

      return !_.isEqual(requestParams, updatedRequestParams) 
        ? updatedRequestParams
        : requestParams;
    });
  }, [submissionsRequestParams, formId, archived]);

  const getSubmissions = useCallback(
    (page, query) => {
      indexSubmissions(dispatchSubmissionsAction, page, requestParams, query, null, FormsConfig.routeTrack);
    },
    [dispatchSubmissionsAction, requestParams]
  );

  useEffect(() => {
    getSubmissions(1);
  }, [getSubmissions, formId, requestParams, archived]);

  const isLoading = formState.isActive || submissionsState.isActive || !paramsSet;

  if (isLoading) {
    return <Loading />;
  }

  const getGridSubmissionsData = () => {
    const routedSubmissions = submissionsState.submissions;

    return _.map(routedSubmissions, (subm) => {
      const inCopy = _.chain(subm).get('data.currentRoute.readOnlyUsers', []).some(readOnlyUser => readOnlyUser._id === user._id).value();
      const trackingData = _.get(subm, 'data.tracking', []);
      const currentRoute = inCopy 
        ? _.chain(trackingData)
            .findLast(track => {
              const ccs = _.get(track, 'senderInfo[0].cc', []);
              const bccs = _.get(track, 'senderInfo[0].bcc', [])
              return _.some(ccs, cc => cc._id === user._id) || _.some(bccs, bcc => bcc._id === user._id)
            })
            .value()
        : _.last(trackingData);

      return {
        routeTrackId: subm._id,
        routedSubmissionId: _.get(subm, 'data.routedSubmission.id', ''),
        submissionName: _.get(subm, 'data.routedSubmission.name', ''),
        formId: formId ||  _.get(subm, 'data.routedForm.id', ''),
        formTitle: formId ?  _.get(formState, 'form.title', '') : _.get(subm, 'data.routedForm.title', ''),
        from: _.get(currentRoute, 'senderInfo[0].from[0].data.email'),
        dateSent: _.get(subm, 'data.currentRoute.date', ''),
        inCopy, 
        dateOpened: _.chain(currentRoute).get('recipientsInfo', [])
          .filter(recipientInfo => {
            return inCopy
             ? recipientInfo.opened
             : recipientInfo.opened && _.get(recipientInfo, 'to[0]._id', '') === user._id
          })
          .map(recipientInfo => {
            const openedDate = recipientInfo.dateOpened;
            return {
              date: openedDate,
              formattedDate: new Date(openedDate),
            }
          })
          .minBy('formattedDate')
          .get('date', '')
          .value()
       }})

  }
  const gridSubmissions = submissionsState.submissions.map(subm => ({...subm.data}));

  const addStyleToUnopenedRecords = (gridComp) => {
    gridComp.dataTableReady.promise.then(() => {
      const gridBody = _.get(gridComp, 'formioGrid.tbody', {});
      const rows = _.get(gridBody, 'refs.row', []);
      const rowsData = gridBody.data;

      _.each(rowsData,(data, index) => {
        if (!data.dateOpened && rows[index]) {
          rows[index].classList.add('font-weight-bold');
        }
      })
    })
  }

  const Grid = () => (
    <Form
      submission={ {data: {submissions: getGridSubmissionsData(), selectedSubmissions: statusAction.selectedIds}}}
      src={FormsConfig.getSrc('routedSubmissionsTable')}
      options={{viewIncoming: true, hide: {restore: !archived, moveToArchived: archived}, noSubmit: true, ...formState.options}}
      formReady={(form)=> {
        const gridComp = form.getComponent('submissions');

        form.on('change', ()=> {
          addStyleToUnopenedRecords(gridComp);
        });

        form.on('page', ()=> {
          addStyleToUnopenedRecords(gridComp);
        });
   
        form.on('track', (row)=> {
          const submission = submissionsState.submissions.find(subm => row.routeTrackId === subm._id );
          setTrackingSubmission(submission);
        });

        form.on('open', (rowData)=> {
          if (rowData) {
            //archived ? `/form/${rowData.formId}/archived/incoming/${rowData.routedSubmissionId}` :
            history.push( `/form/${rowData.formId}/incoming/${rowData.routedSubmissionId}`);
          }
        });

        form.on('page', ()=> {
          addSelectedRowsClass(form, form.data.selectedSubmissions, selectedRowIdProperty);
        });

        form.on('deselectAll', () => {
          addSelectedRowsClass(form, [], selectedRowIdProperty);
        });

        form.on('selectRow', (data)=> {
          const selectedSubmissions = form.data.selectedSubmissions;
          const routeTrackId = data.routeTrackId;

          form.data.selectedSubmissions = _.includes(selectedSubmissions, routeTrackId)
            ? [..._.filter(selectedSubmissions, id => id !== routeTrackId)]
            : [...selectedSubmissions, routeTrackId];

          addSelectedRowsClass(form, form.data.selectedSubmissions, selectedRowIdProperty);
          form.triggerChange();
        });

        
        form.on('moveToArchived', () => {
          setStatusAction({
            ...defaultStatusAction,
            active: true,
            selectedIds:  form.data.selectedSubmissions || [],
            archive: true,
          })
        });

        form.on('restoreArchived', () => {
          setStatusAction({
            ...defaultStatusAction,
            active: true,
            selectedIds:  form.data.selectedSubmissions || [],
            restore: true
          })
        });
      }}
    />
  );

  const onNo = () => {
    setStatusAction({
      ...defaultStatusAction,
      selectedIds: statusAction.selectedIds,
    })
  };

  const onYes = () => {
    const {selectedIds, restore, archive} = statusAction;
    const updatedSubmissions = _.chain(submissionsState.submissions)
      .filter(subm => _.some(selectedIds, id => subm._id === id))
      .each(subm => {
        if (archive) {
          setRouteTrackArchiveUser(subm, user);
        }
        if (restore) {
          removeRouteTrackArchiveUser(subm, user);
        }
      })
      .value();

    saveSubmissions(dispatchSubmissionsAction, updatedSubmissions, null, FormsConfig.routeTrack, (err) => {
      if(!err) {
        const {restore, archive} = statusAction;
        getSubmissions(1);
        setStatusAction(defaultStatusAction);
        addAlert({ type: 'success', content: `Records are successfully ${(restore && 'restored') || (archive && 'archived') || 'updated'} `}); 
      }
    });
  } 

  const titleText = formId && !hideTitle ? `Incoming Submissions - ${formState.form.title} Form ` : '';

  const getConfirmationMessage = () => {
    const {archive, restore} = statusAction;

    if (archive) {
      return <span>Are you sure you wish to <strong>archive all selected records</strong>?</span>;
    }
    
    if (restore) {
      return <span>Are you sure you wish to move <strong>all selected records</strong> to <strong>All Incoming</strong>?</span>;
    }
  };


  const MainContent = () =>  {
    return (
    <div className='form-index position-relative'>
      {titleText && <Title text={titleText} />}
      {statusAction.active && <ModalConfirmation
        onNo={onNo}
        onYes={onYes}
        message={getConfirmationMessage()}
        onModalCloseClick={onNo}
      />}
      <Errors errors={[formState.error, submissionsState.error]} />
      {trackingSubmission && <RouteTrackingModal routeTrack={trackingSubmission} onModalCloseClick={()=> setTrackingSubmission(null)} />}
      {!_.isEmpty(gridSubmissions) ? <Grid/> : 'No items to display.'}
    </div>
  )};

 

  if (formState.error && FormError) {
    return(
      <FormError error={formState.error}>
        <MainContent />
      </FormError>
    );
  }

  return <MainContent />;
}

IncomingSubmissionsList.defaultProps = {
  submissionsRequestParams: {}
};

export default IncomingSubmissionsList;
