import React, { useEffect, useCallback, useState } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom'
import { Errors, Form} from '@formio/react';
import { Loading, Title } from '../../../../common';
import { useForm, getGridForm} from '../../form';
import { useSubmissions, indexSubmissions } from '../submissionsContext';
import { FormsConfig } from '../../../../config';
import _ from 'lodash'
import {Utils} from 'formiojs';

const SubmissionsList = (props) => {
  const {
    FormError,
    formName,
    getViewPath,
    createSubmissionPath,
    addBtnTitle,
    id,
    submissionsRequestParams,
    title,
    hideAddBtn,
    beforeViewSubmission =() => {}
  } = props;
  
  const history = useHistory();
  let formId  = useParams().formId || id;
  
  const defaultRequestParams = {
    limit: 100000,
    query: {},
    select: '',
    sort: '',
    ...submissionsRequestParams
  };
  
  const [requestParams, setRequestParams] = useState(defaultRequestParams);
  const [paramsSet, setParamsSet] = useState(false);
  const { state: submissionsState, dispatch: dispatchSubmissionsAction } = useSubmissions();
  const { state: formState, dispatch: dispatchFormEvent } = useForm();

  useEffect(()=> {
    setRequestParams((requestParams) => {
      const updatedRequestParams = {...requestParams, ...submissionsRequestParams};
      setParamsSet(true);

      return (!_.isEqual(requestParams, updatedRequestParams)) 
        ? updatedRequestParams
        : requestParams;
    });
  }, [submissionsRequestParams]);


  const getFormattedForm = () => {
    const form = formState.gridForm;

    if (form.components){
      const gridComponents = [];

      Utils.eachComponent(formState.form.components, (comp) => {
        if (comp.tableView) {
          gridComponents.push({...comp, calculateValue: ''});
        }
      }, true);

      form.components[0].components = gridComponents;
    }
    return form;
  };

  // const onAction = (submission, action) => {
  //   switch(action) {
  //     case 'view':
  //     case 'row':
  //       history.push(getViewPath ? getViewPath(formId, submission) : `/form/${formId}/submission/${submission._id}`);
  //       break;
  //     case 'edit':
  //       history.push(getEditPath ? getEditPath(formId, submission) : `/form/${formId}/submission/${submission._id}/edit`);
  //       break;
  //     case 'delete':
  //       history.push(getDeletePath ? getDeletePath(formId, submission) : `/form/${formId}/submission/${submission._id}/delete`);
  //       break;
  //     default:
  //   }
  // };

  const getSubmissions = useCallback(
    (page, query) => {
      indexSubmissions(dispatchSubmissionsAction, page, requestParams, query, formId, formName)},
    [dispatchSubmissionsAction, formId, formName, requestParams],
  );

  // const onPageSizeChanged = (pageSize) => {
  //   setRequestParams({ ...requestParams, limit: +pageSize });
  // };

  useEffect(() => {
    getSubmissions(1);
  }, [getSubmissions, formId,requestParams]);

  useEffect(() => {
    getGridForm(dispatchFormEvent, '',FormsConfig.submissionsTable)
  }, [dispatchFormEvent]);
 
  const isLoading = formState.isActive || submissionsState.isActive || !paramsSet;

  if (isLoading) {
    return (<Loading />);
  }

  const gridSubmissions =  submissionsState.submissions.map(subm => subm.data);
  const Grid = () =>  (
    <Form
      submission={ {data: {submissions:gridSubmissions}}}
      form={getFormattedForm()}
      options={{
        noSubmit: true, 
        readOnly: true, 
        ...formState.options
      }}
      formReady={(form)=> {
        form.on('row:click', (row, rowIndex)=> {
          const submission = submissionsState.submissions[rowIndex];

          if (submission) {
            beforeViewSubmission();
            history.push(getViewPath ? getViewPath(formId, submission) : `/form/${formId}/submission/${submission._id}`);
          }
        });
      }}
    />
  );

  const titleText = title || `Current Submissions - ${formState.form.title} Form `;

  const AddBtn = () => {
    return hideAddBtn
      ? null
      : (
        <div className="d-flex justify-content-end mb-2">
          <Link className='btn btn-primary' to={createSubmissionPath || `/form/${formId || formState.id}`}>
            <i className='glyphicon glyphicon-plus fa fa-plus' aria-hidden='true'></i>
            &nbsp; {addBtnTitle || 'New ' + formState.form?.title}
          </Link>
        </div>
      )
  }
  const MainContent = () =>  (
    <div className='form-index'>
      <Errors errors={[formState.error, submissionsState.error]} />
      <Title text={titleText} />
      <AddBtn/>
      {!_.isEmpty(gridSubmissions) ? <Grid/> : 'No items to display.'}
    </div>
  );

  if (formState.error && FormError) {
    return(
      <FormError error={formState.error}>
        <MainContent />
      </FormError>
    );
  }

  return <MainContent />;
}

SubmissionsList.defaultProps = {
  submissionsRequestParams: {}
};

export default SubmissionsList;
