import React, { useEffect, useCallback } from 'react'
import { useHistory } from 'react-router-dom'
import {  Form } from '@formio/react';

import { Loading } from '../../../../common';
import { useForms, indexForms,  getFoldersForms} from '../formsContext';
import { FormsConfig } from '../../../../config';
import { useAuth } from '../../../auth';
import { hasAdminRights } from '../../../../utils';

const FormsList = () => {
  const history = useHistory();
  const {state: formsState, dispatch: dispatchFormsAction} = useForms();
  const {requestParams} = formsState;
  const {state: authState} = useAuth();
  const {is: role} = authState;

  const isAdmin = hasAdminRights(role);

  const getForms = useCallback(
    (page, params) => {
      const args = [dispatchFormsAction, requestParams, page, params];
      isAdmin ? indexForms(...args) : getFoldersForms(...args);;
    },
    [requestParams, dispatchFormsAction, isAdmin]
  );

  useEffect(() => {
    getForms(1);
  }, [getForms, requestParams]);

  const {isActive} = formsState;

  const FormsGrid = () => {
    return (
      <Form
        submission={{data: {forms: formsState.forms}}}
        src={FormsConfig.getSrc(isAdmin ? 'adminFormsTable':'formsTable')}
        options={{noSubmit: true}}
        formReady={(form)=> {
          form.on('row:click', (row)=> {
            history.push(isAdmin ? `/form/${row.formId || row._id}/submission` : `/form/${row.formId || row._id}`);
          })
        }}
      />
    )
  }

  return (
    <div>
      {isActive 
        ? <Loading /> 
        : formsState.forms?.length 
          ? <FormsGrid/> 
          : 'No forms found'
      } 
    </div>
  );
};

export default FormsList;
