import React, {useEffect, useCallback} from 'react'
import { useHistory} from 'react-router-dom';
import { Form} from '@formio/react';

import {Loading} from '../../../../common';
import {useForms} from '../../../forms/form/formsContext';
import {FormsConfig} from '../../../../config';
import {getMyForms } from '../myFormsContext';

const MyFormsList = () => {
  const history = useHistory();

  const {state: formsState, dispatch: dispatchFormsAction} = useForms();
  const {requestParams} = formsState;

  const getForms = useCallback(
    (page, params) => {
      getMyForms(dispatchFormsAction, requestParams, page, params);
    },
    [requestParams, dispatchFormsAction],
  );

  useEffect(() => {
    getForms(1);
  }, [getForms]);

  const { isActive } = formsState;

  const FormsGrid = () => {
    return (
      <Form
      submission={ {data: {forms: formsState.forms}}}
      src={FormsConfig.getSrc('myFormsTable')}
      options={{noSubmit: true}}
      formReady={(form)=> {
        form.on('row:click', (row)=> {
          history.push(`/form/${row.formId || row._id}`);
        });
      }}
    />)
  }

  return (
    <div>
      {isActive ? <Loading /> : formsState.forms?.length ? <FormsGrid/> : 'No forms found'}
    </div>
  );
};

export default MyFormsList;
