import React from 'react';
import _ from 'lodash';
import * as Utils from '../../../utils';
import NativePromise from 'native-promise-only';

const SubmissionsContext = React.createContext();

const initialState = {
  error: '',
  formId: '',
  formName: '',
  isActive: false,
  pagination: {
    numPages: 0,
    page: 1,
    total: 0,
  },
  submissions: [],
};

const SubmissionsReducer = (state, action) => {
  switch (action.type) {
    case 'SUBMISSIONS_RESET':
      return initialState;
    case 'SUBMISSIONS_REQUEST':
      return {
        ...state,
        error: '',
        formId: action.formId,
        formName: action.formName,
        isActive: true,
        pagination: {
          ...state.pagination,
          page: action.page,
        },
        submissions: [],
      };
    case 'SUBMISSIONS_SUCCESS': {
      const total = action.submissions.serverCount;

      return {
        ...state,
        isActive: false,
        pagination: {
          ...state.pagination,
          numPages: Math.ceil(total / action.limit),
          total,
        },
        submissions: action.submissions,
      };
    }
    case 'SUBMISSIONS_FAILURE':
      return {
        ...state,
        error: action.error,
        isActive: false,
      };
    case 'SUBMISSIONS_ACTION_START':
      return {
        ...state,
        error: '',
        isActive: true,
      };
    default:
      return state;
  }
};

export function SubmissionsProvider(props) {
  const [state, dispatch] = React.useReducer(SubmissionsReducer, initialState);
  const value = React.useMemo(() => [state, dispatch], [state, dispatch]);

  return <SubmissionsContext.Provider value={value} {...props} />;
}

export function useSubmissions() {
  const context = React.useContext(SubmissionsContext);
  if (!context) {
    throw new Error('useSubmissions must be used within a SubmissionsProvider');
  }

  const [state, dispatch] = context;

  return {
    state,
    dispatch
  }
}

export const resetSubmissions = () => ({
  type: 'SUBMISSIONS_RESET',
});

const requestSubmissions = (page, params, formId, formName) => ({
  type: 'SUBMISSIONS_REQUEST',
  page,
  params,
  formId,
  formName
});

const receiveSubmissions = (submissions, limit) => ({
  type: 'SUBMISSIONS_SUCCESS',
  submissions,
  limit
});

const failSubmissions = (error) => ({
  type: 'SUBMISSIONS_FAILURE',
  error,
});

const submissionsActionStart = () => ({
  type: 'SUBMISSIONS_ACTION_START',
});

export const indexSubmissions = (dispatch, page = 0, { limit, query, select, sort },  params = {}, formId, formName, done = () => {}) => {
  dispatch(requestSubmissions(page, params, formId), formName);

  return Utils.getSubmissions({ limit, query, select, sort }, params, formId, formName)
    .then((result) => {
      dispatch(receiveSubmissions(result, limit));
      done(null, result);
    })
    .catch((error) => {
      dispatch(failSubmissions(error));
      done(error);
    });
};


export const saveSubmissions = (dispatch, submissions = [], formId, formName, done=()=> {} ) => {
  dispatch(submissionsActionStart());

  NativePromise.all(_.map(submissions, subm => Utils.saveSubmission(subm, formId, formName)))
    .then(() => {
      dispatch(resetSubmissions());
      done();
    })
    .catch((err) => {
      dispatch(failSubmissions(err));
      done(err);
    });
}

export const deleteSubmissions = (dispatch, submissionIds = [], formId, done=()=> {} ) => {
  dispatch(submissionsActionStart());

  NativePromise.all(_.map(submissionIds, id => Utils.deleteSubmission(id, formId)))
    .then(() => {
      dispatch(resetSubmissions());
      done();
    })
    .catch((err) => {
      dispatch(failSubmissions(err));
      done(err);
    });
}
