import React, { useEffect, useCallback, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Errors, Form} from '@formio/react';
import _ from 'lodash';

import { Loading, Title} from '../../../../common';
import { useForm } from '../../form';
import { useSubmissions, indexSubmissions, saveSubmissions, deleteSubmissions } from '../../submission/submissionsContext';
import { FormsConfig } from '../../../../config';
import { ModalConfirmation } from '../../partials';
import { addSelectedRowsClass, setSubmissionStatus, submissionStatuses, unsetSubmissionStatus } from '../../../../utils';
import { useAlerts } from '../../../alerts';

const DeletedSubmissionsList = (props) => {
  const {
    submissionsRequestParams = {},
  } = props;
  
  const history = useHistory();
  const formId  = useParams().formId;

  const defaultRequestParams = {
    limit: 100000,
    query: {},
    select: '',
    sort: '-modified',
    ...submissionsRequestParams
  };
  
  const defaultStatusAction = {
    active:false,
    selectedIds: [],
    deletePermanently: false,
    archive: false,
    moveToCurrent: false,
  };
  const [requestParams, setRequestParams] = useState(defaultRequestParams);
  const [paramsSet, setParamsSet] = useState(false);

  const [statusAction, setStatusAction] = useState(defaultStatusAction);

  const { addAlert } = useAlerts();

  const { state: submissionsState, dispatch: dispatchSubmissionsAction } = useSubmissions();
  const { state: formState} = useForm(defaultStatusAction);

  useEffect(()=> {
    setRequestParams((requestParams) => {
      const updatedRequestParams = {...requestParams, ...submissionsRequestParams};
      setParamsSet(true);

      return !_.isEqual(requestParams, updatedRequestParams) 
        ? updatedRequestParams
        : requestParams;
    });
  }, [submissionsRequestParams]);

  const getSubmissions = useCallback(
    (page, query) => {
      indexSubmissions(dispatchSubmissionsAction, page, requestParams, query, formId);
    },
    [dispatchSubmissionsAction, formId, requestParams]
  );

  useEffect(() => {
    getSubmissions(1);
  }, [getSubmissions, formId,requestParams]);
 
  const isLoading = formState.isActive || submissionsState.isActive || !paramsSet;

  if (isLoading) {
    return <Loading />;
  }

  const gridSubmissions = submissionsState.submissions.map(subm => ({...subm, ...subm.metadata}));

  const onStatusChanged = () => {
    getSubmissions(1);
    setStatusAction(defaultStatusAction);
  }

  const onNo = () => {
    setStatusAction({
      ...defaultStatusAction,
      selectedIds: statusAction.selectedIds,
    })
  };

  const onYes = () => {
    const {deletePermanently, archive, moveToCurrent, selectedIds} = statusAction;

    if (moveToCurrent || archive) {
      const newStatus = archive ? submissionStatuses.archived : '';

      const updatedSubmissions = _.chain(submissionsState.submissions)
        .filter(subm => _.some(selectedIds, id => subm._id === id))
        .each(subm => {
          newStatus 
          ? setSubmissionStatus(subm, newStatus)
          : unsetSubmissionStatus(subm)
        })
        .value();

      saveSubmissions(dispatchSubmissionsAction, updatedSubmissions, formId, null, (err) => {
        if(!err) {
          onStatusChanged();
          addAlert({ type: 'success', content: `Form Submissions are successfully moved to the ${(moveToCurrent && 'Current') || (archive && 'Archived')}`}); 
        }
      });
    } 
    else if (deletePermanently) {
      deleteSubmissions(dispatchSubmissionsAction, selectedIds, formId, (err) => {
        if(!err) {
          onStatusChanged();
          addAlert({ type: 'success', content: 'Form Submissions are successfully deleted'});
        }
      });
    }
  }

  const Grid = () => (
    <Form
      submission={ {data: {submissions: gridSubmissions, selectedSubmissions: statusAction.selectedIds}}}
      src={FormsConfig.getSrc('namedSubmissionsTable')}
      options={{noSubmit: true, hide: {moveToDeleted: true, track:true}, ...formState.options}}
      formReady={(form)=> {
        form.on('open', ({_id: submissionId})=> {
          if (submissionId) {
            history.push(`/form/${formId}/submission/${submissionId}`);
          }
        });

        form.on('page', ()=> {
          addSelectedRowsClass(form, form.data.selectedSubmissions);
        });

        form.on('deselectAll', () => {
          addSelectedRowsClass(form, []);
        });

        form.on('moveToArchived', () => {
          setStatusAction({
            ...defaultStatusAction,
            active: true,
            selectedIds:  form.data.selectedSubmissions || [],
            archive: true,
          })
        });

        form.on('moveToCurrent', () => {
          setStatusAction({
            ...defaultStatusAction,
            active: true,
            selectedIds:  form.data.selectedSubmissions || [],
            moveToCurrent: true,
          })
        });

        form.on('deletePermanently', () => {
          setStatusAction({
            ...defaultStatusAction,
            active: true,
            selectedIds:  form.data.selectedSubmissions || [],
            deletePermanently: true,
          })
        });
 
        form.on('selectRow', (data)=> {
          const selectedSubmissions = form.data.selectedSubmissions;

          form.data.selectedSubmissions = _.includes(selectedSubmissions, data._id )
            ? [..._.filter(selectedSubmissions, id => id !== data._id)]
            : [...selectedSubmissions, data._id];

          addSelectedRowsClass(form, form.data.selectedSubmissions);
          form.triggerChange();
        });
      }}
    />
  );

  const titleText = `Deleted Submissions - ${formState.form.title} Form `;

  const getConfirmationMessage = () => {
    const {deletePermanently, archive, moveToCurrent} = statusAction;

    if (deletePermanently) {
      return <span>You will not be able to restore form submissions after permanent deletion. Are you sure you wish to delete <strong>all selected form submission(s) permanently</strong>?</span>;
    }

    if (archive) {
      return <span>Are you sure you wish to move <strong>all selected form submission(s)</strong> from the <strong>Deleted</strong> to the <strong>Archived</strong>?</span>;
    }

    if (moveToCurrent) {
      return <span>Are you sure you wish to move <strong>all selected form submission(s)</strong> from the <strong>Deleted</strong> to the <strong>Current</strong>?</span>;
    }
  };

  const MainContent = () =>  (
    <div className='form-index position-relative'>
      {statusAction.active && <ModalConfirmation
        onNo={onNo}
        onYes={onYes}
        message={getConfirmationMessage()}
        onModalCloseClick={onNo}
      />}
      <Errors errors={[formState.error, submissionsState.error]} />
      <Title text={titleText} />
      {!_.isEmpty(gridSubmissions) ? <Grid/> : 'No items to display.'}
    </div>
  );

  return <MainContent/>;
}


export default DeletedSubmissionsList;
