import React, { useEffect, useCallback, useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Errors, Form} from '@formio/react';
import _ from 'lodash';

import { Loading, Title } from '../../../../common';
import { useForm } from '../../form';
import { useSubmissions, indexSubmissions } from '../../submission/submissionsContext';
import { FormsConfig } from '../../../../config';
import { RouteTrackingModal } from '../../partials';

const DataRoutesList = (props) => {
  const {
    FormError,
    id,
    submissionsRequestParams,
  } = props;
  
  const history = useHistory();
  const formId  = useParams().formId || id;

  const defaultRequestParams = {
    limit: 100000,
    query: formId ? {'data.routedForm.id': formId} : {},
    select: '',
    sort: '-data.currentRoute.date',
    ...submissionsRequestParams
  };
  
  const [requestParams, setRequestParams] = useState(defaultRequestParams);
  const [paramsSet, setParamsSet] = useState(false);

  const [trackingSubmission, setTrackingSubmission] = useState(null);
  const { state: submissionsState, dispatch: dispatchSubmissionsAction } = useSubmissions();
  const { state: formState} = useForm();

  useEffect(()=> {
    setRequestParams((requestParams) => {
      const updatedRequestParams = {...requestParams, ...submissionsRequestParams};
      setParamsSet(true);

      return !_.isEqual(requestParams, updatedRequestParams) 
        ? updatedRequestParams
        : requestParams;
    });
  }, [submissionsRequestParams, formId]);

  const getSubmissions = useCallback(
    (page, query) => {
      indexSubmissions(dispatchSubmissionsAction, page, requestParams, query, null, FormsConfig.routeTrack);
    },
    [dispatchSubmissionsAction, requestParams]
  );

  useEffect(() => {
    getSubmissions(1);
  }, [getSubmissions, formId,requestParams]);
 
  const isLoading = formState.isActive || submissionsState.isActive  || !paramsSet;

  if (isLoading) {
    return <Loading />;
  }

  const getGridSubmissionsData = () => {
    const routedSubmissions = _.chain(submissionsState.submissions)
      .map(subm => {
        return {
          routeTrackId: subm._id,
          submission: _.get(subm, 'data.routedSubmission'),
          form: _.get(subm, 'data.routedForm'),
          data: _.chain(subm)
            .get('data.tracking', [])
            .last()
            .value()
        };
      })
      .value()

    return _.map(routedSubmissions, (subm) => {
      return {
        routeTrackId: subm.routeTrackId,
        routedSubmissionId: _.get(subm, 'submission.id', ''),
        submissionName: _.get(subm, 'submission.name', ''),
        formTitle: _.get(formId ? formState : subm, 'form.title', ''),
        formId: formId || _.get(subm, 'form.id', ''),
        to: _.chain(subm).get('data.recipientsInfo', []).map(owner => _.get(owner, 'to[0].data.email', '')).value(),
        dateSent: _.get(subm, 'data.senderInfo[0].dateSent', ''),
        from:  _.chain(subm).get('data.senderInfo[0].from[0].data.email', []).value()
      }
    });
  }

  const Grid = () => (
    <Form
      submission={ {data: {submissions: getGridSubmissionsData()}}}
      src={FormsConfig.getSrc('routedSubmissionsTable')}
      options={{adminView: true, hide: {select: true},noSubmit: true, ...formState.options}}
      formReady={(form)=> {
        form.on('track', (row)=> {
          const submission = submissionsState.submissions.find(subm => row.routeTrackId === subm._id);
          setTrackingSubmission(submission);
        })
        form.on('open', (rowData)=> {
          if (rowData) {
            history.push(`/form/${rowData.formId}/routes/${rowData.routedSubmissionId}`);
          }
        })
      }}
    />
  );

  const titleText =formId ? `Routed Submissions - ${formState.form.title} Form ` : 'All Routed Submissions';

  const MainContent = () =>  (
    <div className='form-index position-relative'>
      <Errors errors={[formState.error, submissionsState.error]} />
      {trackingSubmission && <RouteTrackingModal routeTrack={trackingSubmission} onModalCloseClick={()=> setTrackingSubmission(null)}/>}
      <Title text={titleText} />
      {!_.isEmpty(submissionsState.submissions) ? <Grid/> : 'No items to display.'}
    </div>
  );

  if (formState.error && FormError) {
    return(
      <FormError error={formState.error}>
        <MainContent />
      </FormError>
    );
  }

  return <MainContent />;
}

DataRoutesList.defaultProps = {
  submissionsRequestParams: {}
};

export default DataRoutesList;
