import React, { useEffect, useCallback, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Errors, Form} from '@formio/react';
import _ from 'lodash';

import { Loading, Title } from '../../../../common';
import { useForm } from '../../form';
import { useSubmissions, indexSubmissions } from '../../submission/submissionsContext';
import { FormsConfig } from '../../../../config';
import { useAuth } from '../../../auth';
import { RouteTrackingModal } from '../../partials';
import { getReadOnlyUserRoleData, readOnlyRoles } from '../../FormsUtils';


const TrackedSubmissionsList = (props) => {
  const {
    FormError,
    getViewPath,
    id,
    submissionsRequestParams,
  } = props;

  const history = useHistory();
  const formId  = useParams().formId || id;
  const {state: authState} = useAuth();
  const {user} = authState;
  const defaultRequestParams = {
    limit: 100000,
    query: {
      ...(formId ? {'data.routedForm.id': formId} : {}),
      'data.currentRoute.readOnlyUsers._id': user._id,

    },
    select: '',
    sort: '-data.currentRoute.date',
    ...submissionsRequestParams
  };
  
  const [requestParams, setRequestParams] = useState(defaultRequestParams);
  const [paramsSet, setParamsSet] = useState(false);
  const [trackingSubmission, setTrackingSubmission] = useState(null);
  const { state: submissionsState, dispatch: dispatchSubmissionsAction } = useSubmissions();
  const { state: formState} = useForm();

  useEffect(()=> {
    setRequestParams((requestParams) => {
      const updatedRequestParams = {...requestParams, ...submissionsRequestParams};
      setParamsSet(true);

      return !_.isEqual(requestParams, updatedRequestParams) 
        ? updatedRequestParams
        : requestParams;
    });
  }, [submissionsRequestParams, formId]);

  const getSubmissions = useCallback(
    (page, query) => {
      indexSubmissions(dispatchSubmissionsAction, page, requestParams, query, null, FormsConfig.routeTrack);
    },
    [dispatchSubmissionsAction, requestParams]
  );

  useEffect(() => {
    getSubmissions(1);
  }, [getSubmissions, formId,requestParams]);
 
  const isLoading = formState.isActive || submissionsState.isActive || !paramsSet;

  if (isLoading) {
    return <Loading />;
  }

  const getGridSubmissionsData = () => {
    const routedSubmissions = submissionsState.submissions;

    return _.map(routedSubmissions, (subm) => {
      const trackingData = _.get(subm, 'data.tracking', []);
      const roles = readOnlyRoles;
      const {userRole, prevOwnerTrack, currentRoute} = getReadOnlyUserRoleData(trackingData, user._id)
         
      const inCopy = [roles.cc, roles.bcc].includes(userRole);
      return {
        routeTrackId: subm._id,
        routedSubmissionId: _.get(subm, 'data.routedSubmission.id', ''),
        submissionName: _.get(subm, 'data.routedSubmission.name', ''),
        formId: formId ||  _.get(subm, 'data.routedForm.id', ''),
        formTitle: formId ?  _.get(formState, 'form.title', '') : _.get(subm, 'data.routedForm.title', ''),
        from: _.get(currentRoute, 'senderInfo[0].from[0].data.email'),
        dateSent: _.get(subm, 'data.currentRoute.date', ''),
        dateRerouted: userRole === roles.prevOwner ? _.get(trackingData[prevOwnerTrack+1] || {}, 'senderInfo[0].dateSent', '') : '',
        inCopy, 
        dateOpened: _.chain(currentRoute).get('recipientsInfo', [])
          .filter(recipientInfo => {
            return inCopy
             ? recipientInfo.opened
             : recipientInfo.opened && _.get(recipientInfo, 'to[0]._id', '') === user._id
          })
          .map(recipientInfo => {
            const openedDate = recipientInfo.dateOpened;
            return {
              date: openedDate,
              formattedDate: new Date(openedDate),
            }
          })
          .minBy('formattedDate')
          .get('date', '')
          .value()
       }})
  }
  const gridSubmissions = submissionsState.submissions.map(subm => ({...subm.data}));

  const Grid = () => (
    <Form
      submission={ {data: {submissions: getGridSubmissionsData()}}}
      src={FormsConfig.getSrc('routedSubmissionsTable')}
      options={{viewTracked: true, noSubmit: true, ...formState.options}}
      formReady={(form)=> {
        form.on('track', (row)=> {
          const submission = submissionsState.submissions.find(subm => row.routeTrackId === subm._id );
          setTrackingSubmission(submission);
        });

        form.on('open', (rowData)=> {
          if (rowData) {
            history.push(getViewPath ? getViewPath(rowData) : `/form/${rowData.formId}/tracked/${rowData.routedSubmissionId}`);
          }
        });
      }}
    />
  );

  const titleText = formId ?  `Tracked Submissions - ${formState.form.title} Form ` : 'All Routed Submissions';

  const MainContent = () =>  (
    <div className='form-index position-relative'>
      <Errors errors={[formState.error, submissionsState.error]} />
      {trackingSubmission && <RouteTrackingModal routeTrack={trackingSubmission} onModalCloseClick={()=> setTrackingSubmission(null)} />}
      <Title text={titleText} />
      {!_.isEmpty(gridSubmissions) ? <Grid/> : 'No items to display.'}
    </div>
  );

  if (formState.error && FormError) {
    return(
      <FormError error={formState.error}>
        <MainContent />
      </FormError>
    );
  }

  return <MainContent />;
}

TrackedSubmissionsList.defaultProps = {
  submissionsRequestParams: {}
};

export default TrackedSubmissionsList;
